
section {
  padding: 40px 0;
}

.navbar-brand {
  padding: 0;
}

.star {
  font-size: 16px;
  color: #FFE900;
}

.starbar {
  line-height: 37px;
  cursor: pointer;
}

.starbar span {
  font-size: 24px;
}

.starbar span.rating {
  padding-left: 6px;
}

.btn {
  border-radius: 3px;
  cursor: pointer;
  padding: 0 30px;
  height: 40px;
}

.btn.focus,
.btn:focus {
  -webkit-box-shadow: none;
          box-shadow: none;
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1000;
  display: block;
  float: left;
  padding: 0;
  margin: .125rem 0 0;
  font-size: 1rem;
  color: #212529;
  text-align: left;
  list-style: none;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, .15);
  border-radius: 2px;
}

.dropdown-item {
  display: block;
  width: 100%;
  padding: .40rem 1.5rem;
  clear: both;
  font-weight: 400;
  color: #212529;
  text-align: inherit;
  white-space: nowrap;
  background: 0 0;
  border: 0;
  cursor: pointer;
}


